___CSS_LOADER_EXPORT___.push([module.id, `nav {
    height: 100vh;
    display: flex;
    flex-direction: column;
    gap: 1em;
    padding: 20px;
    background-color: rgb(251,249,247);
    width: clamp(280px, 20vw, 20vw);
}

/* PROFILE  */

nav .profile {
    display: flex;
    gap: 1em;
    align-items: center;
    padding-bottom: 1em;
    /* border-bottom: 3px solid gray; */
}

nav .profile img {
    border: 2px solid #ddd;
    border-radius: 50%;
}

/* TASKS  */

nav .new-task {
    display: flex;
    align-items: center;
    gap: 1em;
    width: 100%;
    padding-bottom: 1.5em;
}

nav .new-task img {
    border-radius: 50%;
    background-color: rgb(20, 250, 20);
}

nav .sort-options {
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin-left: 0.5em;
    padding-bottom: 1.5em;
}

nav .sort-options img {
    max-width: 1.5em;
}

nav .sort-options button {
    display: flex;
    align-items: center;
    gap: 1em;
}

/* PROJECTS  */

nav .new-project {
    display: flex;
    width: 100%;
    align-items: center;
    gap: 1em;
    padding-bottom: 1.5em;
}

nav .new-project img {
    border-radius: 50%;
    background-color: rgb(255, 150, 150);
}

nav .projects-list button {
    display: flex;
    gap: 1em;
    align-items: center;
    width: 100%;
}

nav .projects-list {
    display: flex;
    flex-direction: column;
    gap: 1em;
    margin-left: 0.5em;
}

nav .projects-list img {
    max-width: 1.5em;
}

/* HOVERS  */

button:hover {
    background-color: gray;
    border-radius: 10px;
